*::before,
*::after {
  box-sizing: border-box;
}

* {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-focus-ring-color: rgba(255, 255, 255, 0);
}

body {
  padding: 0;
  margin: 0;
}

header {
  position: sticky;
  top: 0;
  z-index: 9999;
}

.my-header {
  display: flex;
  justify-content: space-between;
  padding-inline: 5rem;
  background-color: #13181d;
  align-items: center;
}

.my-header .title-app {
  color: white;
  font-size: 1.5rem;
  padding: 1.2rem;
  font-family: Poppins, sans-serif;
}

.navbar ul {
  display: flex;
  list-style: none;
}

.navbar ul li a {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
  transition: ease-in-out 0.5s;
  font-family: Quicksand;
  padding: 2rem;
}

.navbar ul li a:hover {
  color: orange;
  border-bottom: 2px solid orange;
}

.toggle-menu {
  color: white;
  font-size: 2rem;
  display: none;
  align-items: center;
  cursor: pointer;
}

.hero-section {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  overflow: hidden;
}

.hero-text {
  position: relative;
  color: white;
  z-index: 10;
  text-align: center;
  padding: 3rem;
}

.hero-text h1 {
  font-size: 2.2rem;
  font-family: Poppins, sans-serif;
}

.hero-text p {
  font-size: 1.2rem;
  text-align: center;
  margin: 0;
  font-family: Quicksand, sans-serif;
}

.hero-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.hero-image::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #13181d69;
  z-index: 1;
}

.catalog-section {
  padding: 5rem;
  padding-block: 6rem;
  padding-block-end: 9rem;
  background-color: #13181d;
}

.title-catalog {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  text-align: center;
  font-size: 1.8rem;
  font-family: Poppins, sans-serif;
}

#container-item {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 3rem;
  padding-block-start: 3rem;
  justify-content: center;
  text-align: center;
  align-items: stretch;
}

#container-item .card {
  background-color: rgba(255, 166, 0, 0.884);
  padding: 2rem;
  border-radius: 2rem;
  box-shadow: 10px 20px 20px rgba(0, 0, 0, 0.459);
  text-align: center;
}

.card img {
  width: 100%;
  height: 200px;
  border-radius: 2rem;
  object-fit: cover;
}

.card h2 {
  margin-block-start: 0;
  font-family: Poppins, sans-serif;
}

.card p {
  font-family: Quicksand, sans-serif;
}

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: orange;

  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;

  font-size: 18px;
  color: white;
  cursor: pointer;
  z-index: 9999;
}

footer p {
  margin: 0;
  padding: 2rem;
  text-align: center;
  color: white;
  background-color: #13181dfa;
  font-family: Quicksand, sans-serif;
}

@media (max-width: 790px) {
  .my-header {
    padding-inline: 1rem;
  }
}

@media (max-width: 670px) {
  .navbar .navbar-menu {
    display: none;
  }

  .navbar .toggle-menu {
    display: flex;
    padding-inline-end: 1rem;
  }

  .navbar ul li {
    margin: 2rem;
  }

  .hero-text h1 {
    font-size: 1.4rem;
  }

  .hero-text p {
    font-size: 1rem;
  }
}

@media (max-width: 420px) {
  .my-header {
    padding-inline: 0;
  }

  .my-header .title-app {
    font-size: 1rem;
  }

  .hero-text {
    padding: 1rem;
  }
}

@keyframes slideMenu {
  from {
    transform: translateX(-100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.navbar-menu.navbar-expand {
  position: fixed;
  left: 0;
  top: 65px;
  width: 100%;
  height: 50vh;
  z-index: 1000;
  background-color: #13181d;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  animation: slideMenu 0.5s ease forwards;
  padding: 0;
  margin: 0;
}

.skip-link {
  position: absolute;
  left: -9999px;
  top: 0;
  z-index: 1000;
  text-decoration: none;
}

.skip-link:focus {
  left: 50%;
  background-color: orange;
  padding: 10px;
  color: #13181d;
}

.navbar ul li a:focus {
  border-bottom: 2px solid orange;
  transition: none;
}
